@import url(https://fonts.googleapis.com/css?family=Concert One);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: darkslategray;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}


.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.roundedImage{
border-radius: 50%;
border-style: solid;
border-color: white;

}

.App-link {
  color: #61dafb;
}

.about-body{
margin:auto;
justify-content: center;
width: 100%;
height: 100%;
margin-top: 50px;


}

.about-grid{
  text-align: center;
  justify-content: center;
  width: 100%;
  height: 100%;

}


.projects-body{
  background: rgba(0, 0, 0, 0);

}

.homeMain{
  position: relative;
  background: rgba(255,255,255,0.7);
}

.thumbnail{
  width: 50px;
  height: 50px;
  margin: 10px;
}

.about-grid-boxes{
  background: rgba(255,255,255,0.7);
}


.about-grid-boxes p {
  text-align: center;
  font-size: 17px;
}
.about-grid-boxes h3 {
  text-align: center;
}

.navStyle {
  background: rgba(0, 0, 0, 0);
  height: 8vh;
  font-size: 1.5vw ;
  transition: background 0.5s ease-in-out; 
  font-family: 'Concert One' ;
  padding-left: 50px;
  padding-right: 50px;
  

}
.navStyle:hover{
  background: black;
  
}

.navbar-brand{
  font-size: 1.5vw;
  
}

.paragraphText{
  font-size: 2vh !important;


}

.container1{
background-image: url(/personalpage/static/media/stream.3f9415a0.jpg);
background-size: cover !important;
background-position: center center;
background-repeat: no-repeat;
height: 100vh;
width: 100vw;
}

@media(max-width: 991px){
.container1{
  height: 150vh;
  width: 100vw;
}
.navStyle{
  height: auto;
  font-size: large;
  width: auto;
}
.navbar-toggler{
  right:15px;
  position: absolute;
  top: 5px;
 
}

.navbar-brand{
  font-size: large;
}

}

.projbody{
  background: rgba(0, 0, 0, 0);


}
.navbar-hamburger{
color: rebeccapurple;

}

.navbar-brand .navbar-nav  .nav-item.active .nav-link,
.navbar-nav .nav-item:hover .nav-link {
  color: teal !important;
}



.jumbotron{
  background: rgba(255,255,255,0.7);


}

.projPage{
margin-top: 50px;


}

.project-a{
  height: 230px;
  margin: 20px;

}
.project-b{
  background: blue;
  height:230px;
  margin: 0px;
}

.sliderPage{
  position: relative;
  background: rgba(255,255,255,0.7);
   width: 90%;
   height: 100%;
   margin-left: 5%;
   padding-top: 5%;
   justify-content: center;
   align-items: center;

}

.sliderbutton{
  background: rgba(0,0,0,0.9);
text-align: center;
color: white;
border: none;
display: inline-block;
font-size: 16px;
padding: 20px;
transition: background 0.8s ease-in-out; 
width: 20vh;
margin-bottom: 40px;
}

.sliderbutton:hover{
  background: rgba(255,255,255,0.6);
  color:black;
}

.test4{
text-align: center;

}
.test4 h6{
  text-align: left;
  padding-left: 10%;
  padding-right: 10%;
}
.centercontent{
  justify-content: center;
align-items: center;
display: flex;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
